.wrapper {
    min-width: 320px;
    max-width: 1200px;
    height: 95vh;
    border-radius: 10px;
    margin: 20px auto;
    border: 1px solid rgb(0, 132, 255);
    box-shadow: 0px 0px 9px 2px;
    background: linear-gradient( rgb(10, 10, 10), transparent 1px), linear-gradient( 90deg, rgb(24, 23, 23), transparent 1px);
	background-size: 20px 20px;
	background-position: center center;
}
